import { Container, Grid, Typography, TextField, Paper, CircularProgress, Backdrop } from "@material-ui/core"
import { useState } from "react"
import useGetGames from "../hooks/useGetGames"

const GameList = () => {

    const [filter, setFilter] = useState('')

    const games = useGetGames();
    if (games === null || !games) return (<Backdrop open={true}><CircularProgress /></Backdrop>)
    return (
        <Container>
            <Typography variant="h4" gutterBottom>Kilpailut</Typography>
            <TextField variant="outlined" size="small" value={filter} placeholder="Suodata radan nimellä" margin="normal" onChange={(e) => setFilter(e.target.value)} />
            {games.filter(fg => fg.course.name.toLowerCase().includes(filter.toLowerCase())).map(g => <SingleGame game={g} key={g.course.name + g.date} />)}
        </Container>
    )
}
const SingleGame = ({ game }) => {
    return (
        <Paper style={{ marginBottom: 10, maxWidth: 700, padding: 10 }} elevation={2}>
            <Typography variant="h5">{game.course.name}</Typography>
            <Typography variant="subtitle1">{game.course.layout}</Typography>
            <Typography variant="subtitle2">{game.date}</Typography>
            <Grid container spacing={2} direction="row" justifyContent="flex-start">
                <div style={{ margin: 10, padding: 5 }}>
                    <strong>
                        <Grid item>Pelaaja</Grid>
                        <Grid item>Tulos</Grid>
                        <Grid item>+/-</Grid>
                        <Grid item>HC</Grid>
                        <Grid item>HC tulos</Grid>
                        <Grid item>Sija</Grid>
                        <Grid item>HC Sija</Grid>
                    </strong>
                </div>
                {game.players.map(p => <SinglePlayer player={p} key={p.name + game.course.date} />)}
            </Grid>

        </Paper>
    )
}
const SinglePlayer = ({ player }) => {
    const tyyli = (player.rankHC === 1) ? { fontWeight: 800, margin: 10, backgroundColor: '#fff3d1', padding: 5 } : { margin: 10, padding: 5 }
    return (
        <div style={tyyli}>
            <Grid item>{player.name}</Grid>
            <Grid item>{player.total}</Grid>
            <Grid item>{player.plusminus}</Grid>
            <Grid item>{Math.round(player.HC * 100) / 100}</Grid>
            <Grid item>{Math.round(player.totalHC * 100) / 100}</Grid>
            <Grid item>{player.rank}</Grid>
            <Grid item>{player.rankHC}</Grid>

        </div>
    )
}
export default GameList