import React from 'react'
import { Typography, Paper, Container, Grid, Button} from '@material-ui/core'
import useStyles from '../hooks/useStyles'
import nalle from '../assets/kiekkonalle.png'
import nalleEvil from '../assets/nalleEvil.png'
import axios from 'axios'
import LoginForm from './LoginForm'
import { useState } from 'react'

const FrontPage = ({logged}) => {
  const tyylit = useStyles();
  const [wrongPassword, setWrongPassword] = useState(false);
  
  const handleLogin = async (username, password) => {
    try {
      const token = await axios.post((process.env.NODE_ENV !== 'production') ?
        'http://localhost:3001/login' :
        'https://risbeegomfkerho.herokuapp.com/login'
        , { username, password })
      console.log(token);
      logged.setToken(token.data);

    } catch (e) {
      console.log('Väärä tunnus tai salasana');
      setWrongPassword(true);
    }
  }
  
  return (
    <Container maxWidth="md">
      <Paper style={tyylit.login} elevation={2}>
        <Typography variant="h4">{(wrongPassword) ? 'Väärä tunnus tai salasana perkele' : 'Teretulemast'}</Typography>
        <Grid container direction="row" justifyContent="space-between" alignItems="center">
          <Grid item xs={6}>
            <img src={(wrongPassword) ? nalleEvil : nalle} alt="Kiekkonalle" width="100%" />
          </Grid>
          <Grid item xs={6}>
            { !logged.logged
              ? <LoginForm handleLogin={handleLogin} />
              : <Button variant="contained" color="primary" onClick={() => logged.clearToken()}>Logout</Button>
            }
          </Grid>
        </Grid>
      </Paper>
    </Container>

  )
}
export default FrontPage