import { CardContent, CardHeader, Container, Typography, Card, Paper, TableContainer, Table, TableRow, TableCell,
    TableHead, TableBody, Backdrop, CircularProgress, Collapse, IconButton, Button } from "@material-ui/core";
import { useState } from "react";
import TextField from "./TextField";
import LineChart from "./LineChart";
import BarChart from "./BarCharts";
import { InsertChartOutlined } from "@material-ui/icons";
import useGetHcList from "../hooks/useGetHcList";


const HandicapList = () => {

    const hcList = useGetHcList();
    const [filter, setFilter] = useState('')

       if (hcList === null) return (<Backdrop open={true}><CircularProgress /></Backdrop>);
    return (
        <Container>
            <Typography variant="h3">Handicapit</Typography>
            <TextField placeholder="Etsi rata" value={filter} onChange={(e) => setFilter(e.target.value)} size="small" />
            {hcList.filter(r => r.course.name.includes(filter))
                .map(i => <Rata key={i.course.name + i.course.layout} rata={i} />)}
        </Container>
    )
}
const Rata = ({ rata }) => {
    const [rataAuki, setRataAuki] = useState(false);
    return (
        <Card component={Paper} elevation={3} style={{ marginBottom: 10 }}>
            <CardHeader
                title={rata.course.name}
                subheader={rata.course.layout}
                action={<IconButton onClick={() => setRataAuki(!rataAuki)}><InsertChartOutlined /></IconButton>}
            />
            <CardContent>
                <TableContainer>
                    <Table size="small" className="taulu">
                        <TableHead>
                            <TableRow>
                                <TableCell>Nimi</TableCell>
                                <TableCell>HC</TableCell>
                                <TableCell>Pelit</TableCell>
                                <TableCell>Keskiarvo</TableCell>
                                <TableCell>Mediaani</TableCell>
                                <TableCell>Viim. 10 kierrosta</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rata.players.sort((a, b) => b.games - a.games)
                                .map(p => <Pelaaja key={p.name + rata.course.name + rata.course.layout} pelaaja={p} />)}
                        </TableBody>
                    </Table>
                </TableContainer>
                <Collapse in={rataAuki} unmountOnExit={true}>
                    <BarChart data={rata.players.map(p => {
                        return {
                            nimi: p.name,
                            keskiarvo: p.average,
                            mediaani: p.median,
                            hc: p.hc
                        }
                    })} />
                    <Button onClick={() => setRataAuki(false)}>Sulje...</Button>
                </Collapse>
            </CardContent>
        </Card>
    )
}
const Pelaaja = ({ pelaaja }) => {
    const { name, games, average, median, lastRounds, hc } = pelaaja;
    const [pelaajaAuki, setPelaajaAuki] = useState(false);
    return (
        <>
            <TableRow onClick={() => setPelaajaAuki(!pelaajaAuki)}>
                <TableCell><b>{name}</b></TableCell>
                <TableCell>{hc}</TableCell>
                <TableCell>{games}</TableCell>
                <TableCell>{Math.round(average * 100) / 100}</TableCell>
                <TableCell>{median}</TableCell>
                <TableCell>{JSON.stringify(lastRounds.slice(0,10)).slice(1, -1).replace(/[,]/g, ', ')}</TableCell>
            </TableRow>
            <TableRow>
                <TableCell style={{ paddingBottom: 0, paddingTop: 0, border: 0 }} colSpan={6}>
                    <Collapse in={pelaajaAuki} unmountOnExit>
                        <LineChart data={lastRounds.map((v,i) => {
                            return { index: i, value: v }
                        } )} />
                    </Collapse>
                </TableCell>
            </TableRow>
        </>
    )
}
export default HandicapList;
