import { Animation, ArgumentScale, Stack } from "@devexpress/dx-react-chart"
import { Chart, ArgumentAxis, ValueAxis, BarSeries, Title, Legend } from "@devexpress/dx-react-chart-material-ui"
import { scaleBand } from '@devexpress/dx-chart-core';
import { Paper } from "@material-ui/core"


const BarChart = ({ data }) => {
    return (
        <Paper>
            <Chart data={data} style={{ maxHeight: 250 }}>
                <ArgumentScale factory={scaleBand} />
                <ArgumentAxis />
                <ValueAxis />
                <Legend />
                <Title 
                    text="Ratadata"
                />
                <BarSeries
                    argumentField="nimi"
                    valueField="keskiarvo"
                    name="Keskiarvo"
                />
                <BarSeries
                    argumentField="nimi"
                    valueField="mediaani"
                    name="Mediaani"
                />
                <Animation />
                <Stack />
            </Chart>
        </Paper>
    )
}
export default BarChart;