import { useEffect, useState } from "react";

const useIsLggedIn = () => {
    const [logged, setLogged] = useState(false);

    const clearToken = () => {
        window.localStorage.clear();
        setLogged(false);
    }
    const setToken = (token) => {
        window.localStorage.setItem('passuToken', token);
        setLogged(true);
    }

    useEffect( () => {
        const token = window.localStorage.getItem('passuToken');
        if (token !== null) setLogged(true);
    }, [])
    return { logged, setToken, clearToken }
}   
export default useIsLggedIn;
