import { useEffect, useState } from "react";
import handicapList from "../services/handicapList";

const useGetHcList = () => {
    const [hcList, setHcList] = useState(null);
    useEffect(() => {
        ( async function() {
            setHcList( await handicapList());
        })();
    }, [])
    return hcList;
}
export default useGetHcList;