import { AppBar, Toolbar } from "@material-ui/core";
import { useEffect } from "react";
import { useState } from "react";
import NaviNappi from "./NaviNappi";

const Navipalkki = () => {
    const [ barOpacity, setBarOpacity ] = useState({ opacity: 1 });

    useEffect( () => {
        window.addEventListener('scroll', () => {
            const newOpa = (1-Number(window.scrollY)/1000)
            if (newOpa < 0.1) setBarOpacity({ visibility: 'hidden' })
            else setBarOpacity({ opacity: newOpa })
        })
    }, [])
    return (
        <AppBar position="sticky" style={barOpacity}>
            <Toolbar style={{ flex: 1, flexDirection: 'row', justifyContent: 'space-between' }}>
                <NaviNappi to="/">Etusivu</NaviNappi>
                <NaviNappi to="/points">Kisataulu</NaviNappi>
                <NaviNappi to="/games">Pelit</NaviNappi>
                <NaviNappi to="/hc">HC</NaviNappi>
                <NaviNappi to="/upload">Lähetä .csv</NaviNappi>
            </Toolbar>
        </AppBar>
    )
}
export default Navipalkki;