import { Backdrop, Button, CircularProgress, Container, Typography, Paper } from "@material-ui/core"
import axios from "axios"
import { useState } from "react"
import TextField from "./TextField"

const UploadCsv = () => {

    const [uploading, setUploading] = useState(false)
    const [uploadResponse, setUploadResponse] = useState(null);

    const onSubmit = (e) => {
        e.preventDefault();
        const data = new FormData()
        data.append('filu', e.target.filu.files[0])
        const token = window.localStorage.getItem('passuToken')
        setUploading(true);
        axios.post((process.env.NODE_ENV !== 'production' ?
            'http://localhost:3001/upload' :
            'https://risbeegomfkerho.herokuapp.com/upload'
        ), data, {
            headers: { Authorization: 'bearer ' + token }
        }).then(res => {
            console.log(res.data)
            setUploadResponse(res.data);
        }).catch(e => {
            console.log('Error', e)
        }).finally(() => {
            setUploading(false);
        })
    }

    return (
        <Container maxWidth="md">
            <Backdrop open={uploading}>
                <CircularProgress />
            </Backdrop>
            {(uploadResponse !== null && <UploadStats stats={uploadResponse} />)}
            <Typography variant="h4" gutterBottom>Lähetä .csv</Typography>
            <Typography paragraph>
                Lähetä Udiscin .csv tiedosto
            </Typography>
            <form onSubmit={onSubmit}>
                <TextField type="file" name="filu" /><br />
                <Button type="submit" variant="contained" color="primary">Lähetä</Button>
            </form>
        </Container>

    )
}
const UploadStats = ({ stats }) => {

    return (
        <Paper style={{ padding: 10, marginBottom: 10 }} elevation={3}>
            <Typography variant="h4">Pelejä lisätty: <b>{stats.uusia}</b></Typography>
            <Typography variant="h5">Duplikaatteja: <b>{stats.duplikaatteja}</b></Typography>

            <Typography variant="h5">Hylätyt tuloskortit</Typography>
            <ul>
                <li>Väärä päivä: {stats.wrongDate} kpl</li>
                <li>Väärä nimi: {stats.wrongName.count} kpl</li>
                <ul>
                    {stats.wrongName.names.map(name => <li key={name}>{name}</li>)}
                </ul>
            </ul>
        </Paper>
    )
}
export default UploadCsv