import { BrowserRouter, Switch, Route } from "react-router-dom";
import FrontPage from "./components/FrontPage";
import GameList from "./components/GameList";
import HandicapList from "./components/HandicapList";
import Kilpataulukko from "./components/Kilpataulukko";
import Navipalkki from "./components/Navipalkki";
import UploadCsv from "./components/UploadCsv";
import useIsLggedIn from "./hooks/useIsLoggedIn";

const App = () => {

  const logged = useIsLggedIn();

  return (
    <BrowserRouter>

      {(logged.logged && <Navipalkki />)}
      <Switch>
        <Route path="/games">
          <GameList />
        </Route>
        <Route path="/points">
          <Kilpataulukko />
        </Route>
        <Route path="/hc">
          <HandicapList />
        </Route>
        <Route path="/upload">
          <UploadCsv />
        </Route>
        <Route path="/">
          <FrontPage logged={logged} />
        </Route>
      </Switch>
    </BrowserRouter>
  )
}

export default App;
