import { Button } from "@material-ui/core";
import { useState } from "react";
import TextField from "./TextField";

const LoginForm = ({ handleLogin }) => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

    return (<div>
        <TextField placeholder="Tunnus..." name="username" value={username} onChange={(e) => setUsername(e.target.value)} /><br/>
        <TextField placeholder="Salasana" name="password" value={password} onChange={(e) => setPassword(e.target.value)} /><br/>
        <Button variant="contained" color="primary" onClick={() => handleLogin(username, password)} >Kirjaudu</Button>
    </div>);
}
export default LoginForm;